
.app-container {
    padding-bottom: 48px;
}

.help-me .form {
    margin: 48px auto;
    width: 640px;
}

.errand-item {
    border: 1px solid lightgrey;
    border-radius: 18px;
    box-shadow: 0 0 8px lightgrey;
    width: 720px;
    margin: 18px auto;
    padding: 18px 36px;
    cursor: pointer;
    background-color: #f7f7f7;
}

.errand-item div {
    margin-bottom: 4px;
}

.page-title {
    font-size: 28px;
    text-align: center;
    margin-bottom: 32px;
}

.my-page .form {
    width: 480px;
    margin: 0 auto;
    margin-bottom: 48px;
}

.button-CTA {
    min-width: 140px;
    height: 40px;
    border-radius: 100px;
    border: none;
    background-color: #FFEC19;
    box-shadow: 0 0 4px lightgrey;
    font-size: 18px;
}

.service-examples {
    background-color: #f5f5f5;
}

.service-examples .item {
    overflow: hidden;
    width: 100%;
    height: 400px;
}

.service-examples img {
    opacity: 0.4;
    width: 100%;
}

.service-examples h4 {
    color: black;
    font-size: 24px;
}

.service-examples p {
    color: black;
    margin-top: 12px;
    font-size: 18px;
}

.service-examples ol {
    display: none;
}

.security-guide {
    height: 400px;
    background-color: black;
    padding: 48px 96px;
}

.security-guide h3 {
    text-align: center;
    margin-bottom: 60px;
    color: white;
    font-size: 20px;
}

.security-guide .items {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.security-guide div {
    margin: auto;
    text-align: center;
}

.security-guide div img {
    border-radius: 100px;
    height: 140px;
    width: 140px;
    background-color: white;
}

.security-guide div h4 {
    color: white;
    margin-top: 18px;
    font-size: 16px;
}

.security-guide div p {
    color: white;
    font-size: 14px;
}

.door {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 560px;
}

.door p {
    margin: auto;
    text-align: center;
    font-size: 28px;
    color: black;
}

.footer {
    background-color: #f5f5f5;
    padding: 12px 96px;
    height: 48px;
    font-size: 14px;
    position: fixed;
    bottom: 0;
    width: 100%;
    color: grey;
}

.carousel-caption {
    bottom: 25% !important;
}

.carousel-caption button {
    margin-top: 12px;
}

.ads-bar {
    position: fixed;
    right: 48px;
    top: 224px;
    width: 128px;
    height: 356px;
    padding: 28px 0;
    color: white;
    background-color: #0A317D;
    overflow: hidden;
    outline: 1px solid white;
    outline-offset: -8px;
    box-shadow: 0 0 4px grey;
}

.ads-bar p {
    text-align: center;
}

.ads-bar img {
    width: 128px;
    z-index: -1;
    position: relative;
    top: -48px;
}
